video {
    object-fit: covr;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
}
 
.hero-container {
    background: url('/src/assets/img-home.png') 
    center center/cover no-repeat;
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.2) ;
    object-fit: contain;
}

.hero-container > h1 {
    color: #fff ;
    font-size: 100px;
    margin-top: -100px;
}
.hero-container > p {
    margin-top: 8px;
    color: #fff;
    font-size: 32px ;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.hero-btns{
    margin-top: 32px;

}

.hero-btns .btn {
    margin-top: 6px;
    
}

.fa-play-circle {
    margin-left: 4px;
}

@media screen and (max-width: 960px){
    .hero-container > h1 {
        
        font-size: 70px;
        margin-top: -150px;
    }
    
}
@media screen and (max-width: 768 ){
    .hero-container > h1 {
        
        font-size: 50px;
        margin-top: -100px;
    }
    .btn-mobile{
        display: block;
        text-decoration: none;
    }
    .btn {
        width: 100%;
        
    }
    
}

